
.nav-head{
	width:100%;
	height:150px;
	background: #fff;
	position:relative;
	color: #333;
	font-size:13px;
}
.nav-head p{
	width:100%;
	text-align: center;
}
.nav-head-img{
	width:80px;
	height:80px;
	border-radius: 40px;
	margin:0 auto 10px auto;
	border:1px solid #d3d3d3;
	overflow: hidden;
}
.nav-head-img img{
	display: block;
	padding: 0;
	margin:0;
	width:100%;
	height: auto;
}
.nav-container{
	font-weight: 500;
}
.nav-container li{
	display: block;
	height:50px;
	width:200px;
	text-align: center;
	line-height: 50px;
	font-size:18px;
	cursor: pointer;
}
.nav-container li:hover{
	background: #259780;
}
.nav-call{
	width:100%;
	height:40px;
	line-height: 40px;
	text-align: center;
}
.nav-call i{
	display:inline-block;
	margin-right:10px;
	cursor: pointer;
}

.showMessage{
	position:fixed;
	top:0;
	left: 0;
	right:0;
	bottom: 0;
	z-index: 999;
}
.showMessage-QRCode{
	margin:30% auto;
	width:400px;
	height:430px;
	border:1px solid #c2c2c2;
	border-radius: 8px;
	background: #fff;
	color:#999;
}
.showMessage-QRCode img{
	width:70%;
	display: block;
	margin:0 auto;
}
.showMessage-enter-active, .showMessage-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s
}
.showMessage-enter, .showMessage-leave-to {
  opacity: 0
}

.app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
*{
  margin:0;
  padding: 0;
}
.index-left{
    position: absolute;
    width:200px;
    border-right: 1px solid #d2d2d2;
    top:0;
    left: 0;
    bottom: 0;
    background: #2aa88f;
    color: #fff;
}
.index-right{
  position:absolute;
  left: 210px;
  top:0;
  right:0;
  bottom: 0;
  overflow: hidden;
}
.blog-enter-active, .blog-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.blog-enter, .blog-leave-to {
  opacity: 0
}

.blog-container{
	padding-top:20px;
	width:800px;
	margin:0 auto;
}
.blog-list-banner{
	width:95%;
	margin:0 auto;
	height: 185px;
	border:1px solid #d2d2d2;
}
.swiper-container{
	width:100%;
	height:100%;
}
.swiper-slide img{
	width:100%;
}
.blog-list{
	width:95%;
	margin:10px auto 0 auto;
	height: 200px;
	border-bottom: 1px solid #d2d2d2;
	overflow: hidden;
}
.blog-list-left{
	width:70%;
	float: left;
}
.blog-list-name{
	height:40px;
	line-height: 40px;
	font-size:20px;
	color:#000;
	font-weight: 500;
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	text-decoration: none;
}
.blog-list-container{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	font-size:16px;
	color:#333;
	line-height: 25px;
}
.blog-list-right{
	width:30%;
	float: right;
}
.blog-list-right img{
	width:125px;
	height:100px;
	display: block;
	border-radius: 8px;
	overflow: hidden;
	border:1px solid #d2d2d2;
	margin:50px auto;
}
.blob-list-head{
	height:35px;
	vertical-align: middle;
	line-height: 35px;
}
.blog-list-time{
	display: inline-block;
    padding-left: 3px;
    color: #969696;
    vertical-align: middle;
}

.dropLoad{
	width:100%;
	height: 100%;
	overflow-x:hidden;
	overflow-y:auto;
}
.dropLoad-loading{
	width:100%;
	height:50px;
	line-height: 50px;
	padding: 0;
	text-align: center;
}

